import React from "react"
import { Row, Col } from "antd"
import ProgressBar from "../../Progress"

const SkillsProgress = () => (
  <div>
    <h2>My Skills</h2>
    <p>
      I have measured my skills based on the time I used each technology and the
      confidence I have with each of these.
    </p>
    <Row gutter={[20, 20]}>
      <Col xs={24} sm={24} md={12}>
        <ProgressBar percent={95} text="ReactJS" />
        <ProgressBar percent={90} text="Javascript" />
        <ProgressBar percent={90} text="VueJS" />
        <ProgressBar percent={85} text="Gatsby" />
      </Col>
      <Col xs={24} sm={24} md={12}>
        <ProgressBar percent={85} text="React Native" />
        <ProgressBar percent={80} text="PrestaShop" />
        <ProgressBar percent={80} text="Wordpress" />
        <ProgressBar percent={70} text="Flutter" />
      </Col>
    </Row>
    <h3>Other Technologies</h3>
    <Row gutter={[20, 20]}>
      <Col xs={24} sm={24} md={12}>
        <ul>
          <li>Svelte</li>
          <li>Reason React</li>
          <li>PHP</li>
          <li>Html</li>
        </ul>
      </Col>
      <Col xs={24} sm={24} md={12}>
        <ul>
          <li>css</li>
          <li>scss</li>
          <li>Styled Components</li>
          <li>Git</li>
        </ul>
      </Col>
    </Row>
  </div>
)

export default SkillsProgress
