import React from "react"
import { Row, Col } from "antd"
import AboutTile from "../../AbouTile"
import { stripTags, domHtml } from "../../../utils/stripTags"
import Config from "../../../../config"

import SEO from "../../Seo"

const pageText = {
  paraOne: `Hi !! My name is Santiago Rodríguez González. I'm a Front End developer passionate about software development with web technologies. I'm a Systems and Computing Engineer at Los Andes University, responsible and creative. I have about 4 years of experience working with libraries and frameworks like React JS, Vue JS, Gatsby, React Native, and Flutter. 
`,
  paraTwo: `I like to be open to learn new things and share my knowledge with more people. I have the ability to constantly increase my productivity through efficient time management and a good organization of ideas. I enjoy working in dynamic and cooperative environments where communication is fundamental. I am focused on my constant growth, personally and professionally.`,
}

const AboutMe = () => {
  const description = `${pageText.paraOne} ${stripTags(pageText.paraTwo)}`
  return (
    <>
      <div>
        <SEO
          title="About"
          description={description}
          path=""
          keywords={[
            "Santiago",
            "Rodriguez",
            "Gonzalez",
            "Front End developer",
            "Javascript",
            "ReactJS",
            "VueJS",
            "Gatsby",
          ]}
        />
        <h1 className="titleSeparate">About Me</h1>
        <p>{pageText.paraOne}</p>
        <p dangerouslySetInnerHTML={domHtml(pageText.paraTwo)} />
      </div>
      <Row gutter={[20, 20]}>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img={Config.pathPrefix + "/colombia.png"}
            height={60}
            alt="location image"
            textH4="Born and grew up in"
            textH3="Bogotá, Colombia"
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img={Config.pathPrefix + "/backpack.png"}
            alt="coffee image"
            textH4="Love to travel "
            textH3="Meet other cultures"
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img={Config.pathPrefix + "/trust.png"}
            alt=""
            textH4="Spending time with "
            textH3="Friends and Family"
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img={Config.pathPrefix + "/idea.png"}
            alt="motorcycle image"
            textH4="Materialize ideas"
            textH3="Solve problems"
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img={Config.pathPrefix + "/web.png"}
            alt="web image"
            textH4="Learn new technologies"
            textH3="Discover new tools"
            height={60}
            width={60}
          />
        </Col>
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img={Config.pathPrefix + "/graduation.png"}
            alt="graduation image"
            textH4="Systems and Computing"
            textH3="Engineer"
            height={60}
            width={60}
          />
        </Col>
      </Row>
    </>
  )
}
export default AboutMe
